import dayjs from 'dayjs';
import isoWeek from 'dayjs/plugin/isoWeek';

dayjs.extend(isoWeek);

import scheduleStudents from '../database/scheduleStudents.json';
import scheduleTeachers from '../database/scheduleTeachers.json';

export function getForms() {
   return [
      { label: '', value: '' },
      { label: '-', value: '-' },
      { label: '', value: '' },
   ]
}

export function getLevels() {
   return [
      { label: '', value: '' },
      { label: '', value: '' },
      { label: '', value: '' },
      { label: '', value: '' },
      { label: '', value: '' },
   ]
}

export function getFaculties() {
   return [
      { label: '- ', value: '- ' },
      { label: ' ', value: ' ' },
      { label: '-  ', value: '-  ' },
      { label: '-  ', value: '-  ' },
      { label: '  ', value: '  ' },
      { label: '    ', value: '    ' },
      { label: '   ', value: '   ' },
   ]
}

export function getWeekStatus() {
   let date = dayjs();
   if (date.isoWeek() % 2 === 0) {
      if (date.isoWeekday() == 7) return "   ";
      return "   ";
   } else {
      if (date.isoWeekday() == 7) return "   ";
      return "   ";
   }
}

export function isEvenWeek() {
   let date = dayjs();
   if (date.isoWeek() % 2 === 0) {
      if (date.isoWeekday() == 7) return false;
      return true;
   } else {
      if (date.isoWeekday() == 7) return true;
      return false;
   }
}

export function currentStudentsPeriod(form) {
   if (form == "") {
      return scheduleStudents.fullTime.period;
   } else if (form == "-") {
      return scheduleStudents.partTime.period;
   } else if (form == "") {
      return scheduleStudents.extramural.period;
   }
}

export function currentTeachersPeriod() {
   return scheduleTeachers.period;
}

export function lastStudentsScheduleUpdate() {
   let date = new Date(scheduleStudents.lastUpdate);
   let days = date.getDate();
   let month = date.getMonth() + 1;
   let year = date.getFullYear();
   let hours = date.getHours();
   let minutes = date.getMinutes();
   let seconds = date.getSeconds();
   if (month < 10) month = "0" + month;
   if (days < 10) days = "0" + days;
   if (hours < 10) hours = "0" + hours;
   if (minutes < 10) minutes = "0" + minutes;
   if (seconds < 10) seconds = "0" + seconds;
   return (days + "." + month + "." + year + " | " + hours + ":" + minutes + ":" + seconds);
}

export function lastTeachersScheduleUpdate() {
   let date = new Date(scheduleTeachers.lastUpdate);
   let days = date.getDate();
   let month = date.getMonth() + 1;
   let year = date.getFullYear();
   let hours = date.getHours();
   let minutes = date.getMinutes();
   let seconds = date.getSeconds();
   if (month < 10) month = "0" + month;
   if (days < 10) days = "0" + days;
   if (hours < 10) hours = "0" + hours;
   if (minutes < 10) minutes = "0" + minutes;
   if (seconds < 10) seconds = "0" + seconds;
   return (days + "." + month + "." + year + " | " + hours + ":" + minutes + ":" + seconds);
}

export function findGroups(form, level, faculty) {
   let groups = [];
   let options = [];

   function filterGroups(items) {
      if (!level && faculty) {
         return items.filter(x => x.faculty === faculty);
      }
      if (level && !faculty) {
         return items.filter(x => x.level === level);
      }
      if (level && faculty) {
         return items.filter(x => x.level === level).filter(x => x.faculty === faculty);
      }
      if (!level && !faculty) {
         return items;
      }
   }

   if (form == "") {
      groups = filterGroups(scheduleStudents.fullTime.groups);
   } else if (form == "-") {
      groups = filterGroups(scheduleStudents.partTime.groups);
   } else if (form == "") {
      groups = filterGroups(scheduleStudents.extramural.groups);
   }

   for (let i = 0; i < groups.length; i++) {
      options.push({ label: groups[i].group, value: groups[i].group })
   }

   return options;
}

export function findTeachers(faculty) {
   let teachers = [];
   let options = [];

   function filterTeachers(items) {
      if (!faculty) {
         return items;
      }
      if (faculty) {
         return items.filter(x => x.faculty === faculty);
      }
   }

   teachers = filterTeachers(scheduleTeachers.teachers);

   for (let i = 0; i < teachers.length; i++) {
      options.push({ label: teachers[i].teacher, value: teachers[i].teacher, disabled: (Object.keys(teachers[i].schedule).length > 0 ? false : true) })
   }

   return options;
}

export function findStudentsSchedule(form, group) {
   if (form == "") {
      return scheduleStudents.fullTime.groups.find(x => x.group === group);
   } else if (form == "-") {
      return scheduleStudents.partTime.groups.find(x => x.group === group);
   } else if (form == "") {
      return scheduleStudents.extramural.groups.find(x => x.group === group);
   }
   return false;
}

export function findTeachersSchedule(teacher) {
   return scheduleTeachers.teachers.find(x => x.teacher === teacher);
}